import React from 'react';
import PropTypes from 'prop-types';
import { Tabs, HorizontalScroll, TabsItem } from '@vkontakte/vkui';

const SortTabs = ({ value: { by }, onSelected }) => (
  <Tabs theme="header" type="buttons">
    <HorizontalScroll>
      <TabsItem
        onClick={() => onSelected({ by: 'popularity' })}
        selected={by === 'popularity'}
      >
        Популярные
      </TabsItem>
      <TabsItem
        onClick={() => onSelected({ by: 'price', ascending: true })}
        selected={by === 'price'}
      >
        Доступные
      </TabsItem>
      <TabsItem
        onClick={() => onSelected({ by: 'rating' })}
        selected={by === 'rating'}
      >
        Лучшие
      </TabsItem>
      <TabsItem
        onClick={() => onSelected({ by: 'experience' })}
        selected={by === 'experience'}
      >
        Опытные
      </TabsItem>
    </HorizontalScroll>
  </Tabs>
);

SortTabs.propTypes = {
  onSelected: PropTypes.func,
  value: PropTypes.shape({
    by: PropTypes.string,
    ascending: PropTypes.bool
  })
};

SortTabs.defaultProps = {
  onSelected: null,
  value: {
    by: 'popularity'
  }
};

export default SortTabs;
